<template lang="html">
  <div class="User-treated">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col span="10" >
        <p>{{$t("common.Username")}}</p>
        <Input  placeholder="" style="width: 150px"></Input>
      </Col>
      <Col span="14">
        <p>{{$t("common.Submission_time")}}</p>
        <DatePicker type="date" placeholder="" style="width: 120px"></DatePicker> —
        <DatePicker type="date" placeholder="" style="width: 120px"></DatePicker>
        <Button type="primary" class="mgl-10">{{$t("common.Query")}}</Button>
      </Col>
    </Row>
    <Table highlight-row :columns="columns1" :data="data1" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  </div>
</template>

<script>
export default {
  name:'User_treated',
  data (){
    return {
      columns1 :[
        {
          title: this.$t('common.SID'),
          key: 'SID',
          align:'center',
          width:90
        },
        {
            title: this.$t('common.Feedback_content'),
            key: 'content',
            align:'content',
        },
        {
            title: this.$t('common.Username'),
            key: 'name',
            align:'name',
            width:200
        },
        {
            title: this.$t('common.Contact_number'),
            key: 'phone',
            align:'phone',
            width:200
        },
        {
            title: this.$t('common.Submission_time'),
            key: 'date',
            align:'center'
        },
        {
            title: this.$t('common.Operate'),
            key: 'operation',
            align:'center',
            render: (h, params) => {
              return h('div', [
                  h('Button', {
                      props: {
                          type: 'text',
                          size: 'small'
                      },
                      style: {
                          marginRight: '10px',
                          color:'#999'
                      },
                      on: {
                          click: () => {
                              //this.show(params.index)
                          }
                      }
                  }, this.$t('common.Solved'))
              ]);
          }
        }
      ],
      data1 :[
        {
          SID:'1',
          content:'普及评级方法',
          name:'黄宗洛',
          phone:'131012345678',
          date:'2018-02-03 10:30:20',
          operation:''
        },
        {
          SID:'2',
          content:'普通评级方法',
          name:'胡歌',
          phone:'131012345678',
          date:'2018-02-03 10:30:20',
          operation:''
        },
        {
          SID:'3',
          content:'普通评级方法',
          name:'张译',
          phone:'131012345678',
          date:'2018-02-03 10:30:20',
          operation:''
        }
      ]
    }
  },
  methods:{
    onedata(){
      //this.$router.push('/Add_article');
    },
    goto (index){
      this.$router.push('/'+index);
    }
  }
}
</script>

<style lang="css">
.User-treated{
  
}
.User-treated .search_criteria p{
  height: 30px;
}
.User-treated .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
</style>



